<template>
  <!-- Extend the instantsearch hierarchicalMenu to provide multi-level category navigation based on design document -->
  <div>
    <div>
      <ul class="category-filter__list">
        <li v-for="item in items" :key="item.value" :class="classObj(item)">
          <router-link
            :to="createURL(item.value)"
            :event="''"
            @click.native="
              handleLinkClick({
                value: item.value,
                to: createURL(item.value),
              })
            "
            >{{ item.label }}</router-link
          >
        </li>
        <!-- <AisHierarchicalMenuChipListSimpleURL
          v-if="hasData(item)"
          :items="[...item.data]"
          :refine="refine"
          :createURL="createURL"
          :level="level + 1"
          :name="name"
        /> -->
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import subCategoryCenter from "@/mixins/subCategoryCenter";

export default {
  name: "AisHierarchicalMenuChipListSimpleURL",
  mixins: [subCategoryCenter],
  props: {
    level: { type: Number, default: 0 },
    items: Array,
    refine: Function,
    createURL: Function,
    name: String,
  },
  methods: {
    ...mapActions(["toggleNoResetModal"]),
    classObj(item) {
      return {
        "category-filter__item": true,
        // "category-filter__item--active": item.isRefined,
        "category-filter__item--selected": item.isRefined,
      };
    },
    handleLinkClick({ value, to }) {
      this.toggleNoResetModal(true);
      this.refine(value);
      this.$router.push({ path: to, hash: "internal" });
    },
  },
};
</script>

<style lang="scss">
.category-filter__list {
  display: flex;

  .category-filter__item {
    a {
      padding: 6px 16px;
      margin-right: 8px;
      border: 1px solid #303133;
      border-radius: 24px;
      font-weight: bold;
    }
    display: inline-flex;
    justify-content: center;
    align-items: flex-start;
    flex-shrink: 0;
    padding-bottom: 0;

    &.category-filter__item--selected {
      > a {
        pointer-events: none;

        background: #303133;
        color: white;
      }
    }
  }
}
</style>
