<template>
  <section class="container">
    <AisCategoryFilterSimpleURL
      :baseFilter="`categories.category_id: 5212`"
      :store="{
        name: 'Flokkar',
        entity_id: 5212,
      }"
      :levels="5"
      attribute="categories"
      rootPath="Flokkar"
    >
      <template #customRender>
        <div class="container gap view-category">
          <FilterAndOptions hideMoreOptions />
          <h2 class="view-category__page-title" key="categoryName">
            {{ categoryName }}
          </h2>
        </div>
      </template>
    </AisCategoryFilterSimpleURL>
  </section>
</template>

<script>
import AisCategoryFilterSimpleURL from "@/components/AisCategoryFilterSimpleURL";
import FilterAndOptions from "@/components/FilterAndOptions";
import { mapGetters, mapActions } from "vuex";
import get from "@/util/get";

export default {
  name: "ViewCategory",
  components: {
    AisCategoryFilterSimpleURL,
    FilterAndOptions,
  },
  computed: {
    ...mapGetters(["urlPathToCategoryMapping"]),
    categoryName() {
      const { $route, urlPathToCategoryMapping } = this;
      const currPath = get($route, "path").slice(1);
      return get(urlPathToCategoryMapping, `${currPath}.name`);
    },
  },
  methods: {
    ...mapActions(["setNavBarTitle"]),
  },
  mounted() {
    this.setNavBarTitle({
      en: this.categoryName,
      is: this.categoryName,
    });
  },
  watch: {
    categoryName(name) {
      this.setNavBarTitle({
        en: name,
        is: name,
      });
    },
  },
};
</script>

<style scoped>
.category-path {
  font-size: 10pt;
}
.category-path .divider {
  margin-left: 2px;
  margin-right: 4px;
}
.category-path span:last-child .link {
  opacity: 0.7;
}
.category-path span:last-child .divider {
  display: none;
}

@media screen and (max-width: 767px) {
  .view-category__page-title {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .view-category {
    margin-top: 40px;
  }
}
</style>
